.NewPost {
    width: 50%;
    margin: 20px auto;
    /* border: 1px solid #eee; */
    /* box-shadow: 3px 4px 3px #ccc , -1px -1px 3px #ccc; */
    text-align: center;
    /* border-radius: 25px; */
    font-family: 'Open Sans', sans-serif;
}
    


.NewPost label {
    display: block;
    margin: 10px auto;
    text-align: center;
    font-weight: bold;
}

.NewPost input,
.NewPost textarea,
.NewPost select {
    display: block;
    width: 80%;
    box-sizing: border-box;
    border: 1px solid black;
    outline: none;
    font: inherit;
    margin: auto;
    /* border-radius: 8px; */
}

.NewPost button {
    margin: 20px 0;
    height: 50px;
    padding: 10px;
    font: inherit;
    border: 1px solid #1A8FE3;
    background-color: transparent;
    color: #1A8FE3;
    cursor: pointer;
}

.NewPost button:hover,
.NewPost button:active {
    color: white;
    background-color: #1A8FE3;
}